                        </el-tab-pane>
                        <el-tab-pane label="院校分数线" name="forth"> 
                            <!-- <div>
                                <div v-if="'forth'==activeName" ref="rankChart" style="width: 600px; height: 300px; padding-left: 250px"></div>
                                <div v-if="'forth'==activeName" ref="gradeChart" style="width: 600px; height: 300px; padding-left: 250px"></div>
                            </div>   -->
                            <div>
                                <div ref="rankChart" style="width: 540px; height: 300px; padding-left: 250px"></div>
