                            </div> 
                        </el-tab-pane>
                    </el-tabs>
                </el-card>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    data() {
      return {
        activeName: 'second',
        introText: "\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0中南大学坐落在中国历史文化名城──湖南省长沙市，占地面积5886亩，建筑面积276万平方米，跨湘江两岸，依巍巍岳麓，临滔滔湘水，环境幽雅，景色宜人，是求知治学的理想园地。  \r\n  \xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0中南大学是教育部直属全国重点大学、国家“211工程”首批重点建设高校、国家“985工程”部省重点共建高水平大学和国家“2011计划”首批牵头高校，2017年9月经国务院批准入选世界一流大学A类建设高校。　　\r\n  \xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0中南大学由原湖南医科大学、长沙铁道学院与中南工业大学于2000年4月合并组建而成。原中南工业大学的前身为创建于1952年的中南矿冶学院，原长沙铁道学院的前身为创建于1953年的中南土木建筑学院，两校的主体学科最早溯源于1903年创办的湖南高等实业学堂的矿科和路科。原湖南医科大学的前身为1914年创建的湘雅医学专门学校，是我国创办最早的西医高等学校之一。　　\r\n  \xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0中南大学秉承百年办学积淀，顺应中国高等教育体制改革大势，弘扬以“知行合一、经世致用”为核心的大学精神，力行“向善、求真、唯美、有容”的校风，坚持自身办学特色，服务国家和社会重大需求，团结奋进，改革创新，追求卓越，综合实力和整体水平大幅提升。　　\r\n  \xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0学校学科门类齐全，拥有完备的有色金属、医学、轨道交通等学科体系，涵盖哲、经、法、教、文、理、工、医、管、艺等10大学科门类，辐射军事学。现有一级学科国家重点学科6个，二级学科国家重点学科12个，国家重点(培育)学科1个，国家临床重点专科61个;设有31个二级学院，104个本科专业;博士学位授权一级学科35个，硕士学位授权一级学科46个，博士专业学位授权类别2个，硕士专业学位授权类别23个，博士后科研流动站32个。材料科学、工程学、临床医学、化学、药理学与毒理学、生物学与生物化学、神经科学与行为学、数学、计算机科学、分子生物学与遗传学、社会科学总论、免疫学、精神病学与心理学、地球科学等14个学科ESI(基本科学指标)排名居全球前1%，其中材料科学排名居全球前1‰;拥有享“南湘雅”美誉的湘雅医院、湘雅二医院、湘雅三医院3所大型三级甲等综合性医院及湘雅口腔医院，湘雅医学院附属肿瘤医院、海口医院、株洲医院。　　\r\n  \xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0学校坚持人才强校战略，师资力量雄厚。有中国科学院院士2人，中国工程院院士14人，国家“万人计划”领军人才17人，“973计划”项目首席科学家19人(其中青年项目2人)，“长江学者奖励计划”特聘、讲座教授52人，国家教学名师7人，教授及相应正高职称人员1500余人，享受政府特殊津贴专家437人。　　\r\n  \xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0学校坚持立德树人，质量优先，努力探索和完善人才培养新模式。现有全日制在校学生5.5万余名，其中本科生3.4万余人、研究生2万余人、留学生近千人。先后获得国家精品课程57门，国家教学团队8个，国家级实验教学示范中心8个。在国内率先创办创新型高级工程人才实验班，成为教育部卓越工程师、卓越医师、卓越法律人才教育培养计划试点高校。学校是全国首批试点开展八年制医学教育(医学博士学位)的大学之一，也是全国第一所为军队培养现役军官指技合一硕士研究生的高校。学校入选全国首批毕业生就业典型经验高校、全国大众创业万众创新示范基地，成为我国百强企业最欢迎的10所大学之一。　　\r\n  \xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0学校坚持瞄准国家和社会重大需求，深入推进协同创新，积极服务国民经济建设和国防现代化建设主战场。学校现有国家级创新平台22个，其中国家重点实验室2个、国家工程研究中心5个、国家工程技术研究中心2个、国家工程实验室6个、国防科技重点实验室1个、国家工程化与创新能力建设平台1个、国家临床医学研究中心3个，牵头和参与组建国家“2011协同创新中心”2个。2000年以来，学校共获国家科技三大奖91项，其中获国家科技一等奖(特等奖)14项，9个项目入选“中国高校十大科技进展”。    \r\n  \xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0学校坚持开放办学，国际合作与交流活跃，先后与美、英、澳、加、日、法、德、俄等30多个国家和地区的200多所大学和科研机构建立了长期合作关系，与众多跨国企业集团广泛开展产学研合作。现有来自100多个国家和地区的留学生在校学习。\r\n  \xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0学校始终坚持党委领导下的校长负责制，牢牢把握正确办学方向，全面提升学校党建与思想政治工作科学化水平，为改革发展稳定提供坚强的思想、政治和组织保证。学校先后被评为全国党的建设和思想政治工作先进高校、全国精神文明建设工作先进单位、全国基层思想政治工作先进典型单位。\r\n  \xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0学校的建设和发展得到了党和国家领导人的亲切关怀和充分肯定。近年来，习近平总书记等9位中央政治局常委在内的20余位党和国家领导人相继来校视察，对学校的改革创新、人才培养、科学研究、社会服务等各项工作给予高度评价。\r\n  \xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0“惟楚有材，于斯为盛”，今天的中南大学正肩负着国家高水平大学建设的历史责任，立足湖南，面向全国，放眼世界，加快推进校内综合改革和内涵式发展，努力建设特色鲜明的世界一流大学，为实现中华民族伟大复兴的中国梦做出更大贡献!",
        teacher: "现有中国科学院院士3人，中国工程院院士12人，博士生导师578人，教授及其他正高职称人员850人，副教授及其他副高职称人员1663人。",
        subjects: "",
        presentRow: {
            ranks: [4215, 4512, 4728, 5050, 4706],
            school_grades: [574, 576, 567, 571, 577, 609, 620, 592, 575, 612, 614, 610, 601, 630],
            ctrl_grades: [544, 547, 535, 536, 534, 567, 572, 520, 495, 522, 526, 517, 505, 513]
        }
      };
    },
    mounted() {
        this.drawCharts() 
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      },

      drawCharts() {
            var rankChart = this.$echarts.init(this.$refs.rankChart);
            var gradeChart = this.$echarts.init(this.$refs.gradeChart);
            var options = {
                title: {
                    text:  "中南大学录取排名变化曲线"
                },
                tooltip: {
                    trigger: 'axis'
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: false,
                    name: '年份',
                    data: ['2014', '2015', '2016', '2017', '2018']
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {
                        formatter: '{value} 名'
                    },
                    name: '录取排名',
                    scale: true
                },
                series: [
                    {
                        name: '录取名次',
                        type: 'line',
                        barWidth: '100%',
                        data: this.presentRow.ranks
                    }
                ]
            };
            rankChart.setOption(options)

            gradeChart.setOption({
                title: {
                    text: "中南大学录取分数线与批次线对比图"
                },
                tooltip: {
                    trigger: "axis"
                },
                xAxis: {
                    boundaryGap: false,
                    name: '年份',
                    data: ['2005', '2006', '2007', '2008', '2009', '2010', '2011', '2012', '2013', '2014', '2015', '2016', '2017', '2018']
                },
                yAxis: {
                    type: 'value',
                    name: '分数线',
                    scale: true,
                    axisLabel: {
                        formatter: '{value} 分'
                    }
                },
                series: [
                    {
                        name: '录取分数线',
                        type: 'line',
                        data: this.presentRow.school_grades
                    },
                    {
                        name: '对应批次线',
                        type: 'line',
                        data: this.presentRow.ctrl_grades
                    }
                ]
            })
    }
    }
  };
</script>

<style scoped>
    .header {
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .info {
        white-space: pre-line;
    }
</style>


